/** @format */
import { useEffect, useRef } from 'react'
import { Bar, Chart, Line } from 'react-chartjs-2'
import { DataTable } from 'simple-datatables'
import {
	Chart as ChartJS,
	CategoryScale,
	LinearScale,
	PointElement,
	LineElement,
	Title,
	Tooltip,
	Filler,
	Legend,
	BarElement,
} from 'chart.js'
import DashbaordLayout from '../../layouts/dashboard-layout'

ChartJS.register(
	CategoryScale,
	LinearScale,
	PointElement,
	LineElement,
	BarElement,
	Title,
	Tooltip,
	Filler,
	Legend
)

